                <div class="columns is-vcentered">
                    <div class="column is-half has-text-centered">
                        <img src="../assets/logo256.jpg" class="hero-img" alt="migao" />
                    </div>
                    <div class="column content index-content is-half has-text-centered-mobile ">
                        <h1 class="title">
                            {{$t('m.hello')}}
                        </h1>
                        <p class="is-size-5 padding-top-1x">
                            {{$t('m.intro')}}
                        </p>
                        <router-link class="button is-light" to="/about"> {{$t('m.aboutme')}} </router-link>
                    </div>
                </div>
            </div>
        </div>
    </section>
</div>
</template>
<style>
.hero-img{
    border-radius: 50%;
}    
</style>
<script>
export default {
    name: 'Hero',
}
</script>


